		.comment{
			.comment_author{
				float: right;
				.star_content{
					float: right;
				}
				.comment_author_infos{
					text-align: right;
					.date-comment{
						@media (max-width: 543px) {/* If media is below 544 */
							margin-right: 15px;
							margin-left: 0;
						}
					}
				}
			}
			.comment_details{
				float: right;
				text-align: right;
				ul{
					li{
						float: right;
						@media (max-width: 991px) {/* If media is below 768 */
							float: none;
						}
						.usefulness_btn{
							margin-right: 5px;
							margin-left: 0;
							@media (max-width: 543px) {/* If media is below 544 */
								margin-right: 0
							}
							&.yes{
								margin-right: 15px;
								margin-left: 0;
								@media (max-width: 543px) {/* If media is below 544 */
									margin-right: 0
								}
							}
						}
						&.comment_report{
							float: left;
							@media (max-width: 991px) {/* If media is below 768 */
								float: none;
							}
						}
					}
				}
			}
		}
	}
}   


/*========================== Product-Page Wish-List ==========================*/

.wishlist_container{
	#wishlist_button{
		select, .product_wishlist_btn{
			float: right;
		}
		.product_wishlist_btn{
			padding: 11px 30px 11.5px 10px;
			span{
				padding-right: 15px;
				padding-left: 0;
				&:before{
					right: -5px;
					left: auto;
				}
			}
		}
	}
}  

.wishlist_login{
		.wishlist_button{
			&:before{
				right: 0;
				left:auto;
			}
			span{
				padding-left: 0px;
				padding-right: 20px;
			}
		}
}
#wishlist_form{
	a{
		i{
			&.close{
				float: left;
			}
		}
	}
}

#WishlistModal{
	.modal-body{
		a{
			float: left;
		}
	}
}


